.About {
    /* border:1px solid orangered; */
    /*display:flex;
    justify-content: space-between;*/
    display: inline-grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    background: linear-gradient(#cacacaa8, 60%,rgba(255, 255, 255, 0));
  
}

.About p{
    margin:5px; 
}

.a{
    text-decoration-line: underline;
    cursor: pointer;
}

.a:hover{
  font-style: italic;
  
}


.contact, .credits{
    font-family: ApfelGrotezk;
    padding-bottom: 30px;
    /* border: 2px solid green; */
}

.bio {
    font-family: ApfelGrotezk;
    /* border: 2px solid blue; */
    grid-column: auto / span 3;    
}

.biohide {
    display:none;
}

.credits-text{
    position:absolute;

}

.credits-texthide{
    display:none;
}
.credits-textshow{
    display: block;


}