.Marquee{

    width: 100vw;
    height: 7vh;
    /* border:1px solid blueviolet; */
    color:black;
    font-family: LackLine-italic;
    font-size:35px;   
    /* position:relative; */
    overflow:hidden;  
    cursor:default; 
    display:flex;
    align-items:center;  
    user-select: none; 
    -moz-user-select: none;
}

.marquee-content{
    margin:0;
    position:absolute;
    left:-50px;
    /* border: 1px solid hotpink; */
    /* width:100vw; */
   display:flex;
   justify-content: center;
    align-items: center; 
    white-space: nowrap;
     font-size: 30px; 

    animation: scrolling 40s linear infinite;   
}



@keyframes scrolling{
    0% {transform: translateX(0);}
    100% {transform: translateX(-100vw);} /* n number of li elements (9) * their width (20vw) = 180 */
}

